.main-header {
    width: 100%;
    position: sticky;
    top: 0;
    left: 0;
    z-index: 10;
}

.basic_links {
    background-color: rgb(230, 227, 227);
    display: flex;
    justify-content: space-between;
    padding: 5px 8%;
    /* z-index: 60; */
}

.basic_links nav ul a {
    display: inline-block;
    list-style: none;
    margin-left: 10px;
    border: 0;
    border-right: 1px solid black;
    background-color: transparent;
    padding: 10px 10px;
    cursor: pointer;
    color: rgb(42, 41, 41);
    text-decoration: none;
    font-size: 14px;
}

.basic_links nav ul a:nth-child(4){
    border: none;
}

.set_location {
    border: none;
    background-color: transparent;
    display: flex;
    align-items: center;
    cursor: pointer;
}

.set_location span:nth-child(1){
    color: rgba(71, 69, 69, 0.944);
}

.set_location span:nth-child(2){
    min-width: 2rem;
    background-color: white;
    /* height: 1rem; */
    padding: 5px 10px;
    margin-left: 5px;
    display: flex;
    border-radius: 15px;
    border: 1px solid black;
}

.set_location span:nth-child(2) p{
    margin-right: 5px;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 6%;
    background: linear-gradient(to right, #00093c, #2d0b00);
    /* background-color: black; */
    /* position: fixed;
    left: 0;
    top: 2rem; */
}

.header img {
    width: 16rem;
    color: white;
    /* background-color: red; */
}

.header input {
    width: 0;
    transition: 1s ease-in;
    padding: 11.5px;
    border: 1px solid #e0e0e0;
    border-radius: 4px 0 0 4px;
    line-height: 1;
    border-right: 0;
    outline: none;
}

.header button {
    padding: 12px 20px;
    font-size: 17px;
    margin-left: -25px;
    background-color: var(--primary-color);
    border: 0;
    border-radius: 3px;
    color: #fff;
    border: 1px solid transparent;
    font-family: "Open Sans",sans-serif;
    cursor: pointer;
}

.header button:hover {
    box-shadow: 0 0 3px var(--primary-color);
}

.header h1 span{
    color: var(--primary-color);
}

.search_btn:hover input{
    width: 30rem;
}

hr {
    opacity: 0.6;
}
